@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@400;700&display=swap);
*,*::before,*::after{font-family:inherit;box-sizing:border-box}:root{--page-background: #E0E0E0;--max-width: 102rem;font-size:10px}body{background:#E0E0E0;background:var(--page-background);font-family:'Big Shoulders Display', sans-serif}

.github{position:fixed;right:2rem;bottom:2rem;width:4rem;height:4rem}.github img{width:100%;height:100%;object-fit:contain;object-position:center;-webkit-filter:drop-shadow(0, 0, 0, rgba(0,0,0,0));filter:drop-shadow(0, 0, 0, rgba(0,0,0,0));transition:transform .4s, -webkit-filter .4s;transition:transform .4s, filter .4s;transition:transform .4s, filter .4s, -webkit-filter .4s}.github:hover img{-webkit-filter:drop-shadow(0 0 0.4rem rgba(0,0,0,0.4));filter:drop-shadow(0 0 0.4rem rgba(0,0,0,0.4));transform:scale(1.2)}.github a{display:block;position:absolute;top:0;left:0;z-index:1;width:100%;height:100%;font-size:0}

.card-list{padding:0;margin:auto;width:80%;max-width:var(--max-width)}.no-match-message{font-size:2rem;text-align:center;padding-top:2rem}

.card{display:inline-block;cursor:help;width:15rem;padding:1.6rem;margin-right:2rem;margin-bottom:2rem;border-radius:1rem;background:#FFF;box-shadow:0 0 0.4rem rgba(0,0,0,0);box-sizing:border-box;font-family:'Big Shoulders Display', sans-serif;transition:transform .2s, box-shadow .2s}.card:hover{transform:scale(1.05);box-shadow:0 0 3rem rgba(0,0,0,0.2)}.card h3{margin:0;font-size:1.4rem;font-weight:400}.card h3 span:not(:last-of-type){margin-right:.4rem}.card h3 .name{font-weight:700}

.img-container{height:100px}.img-container img{display:block;width:100%;height:100%;object-fit:contain}

.type-tag{display:inline-block;width:58px;height:28px;border:2px solid rgba(255,255,255,0.7);border-radius:20px;color:#FFF;font-family:'Big Shoulders Display', sans-serif;font-size:12px;line-height:26px;text-align:center;text-transform:uppercase}.type-tag.type-tag:not(:last-child){margin-right:0}.type-tag.normal{background:#A8A878}.type-tag.normal.disabled{border-color:#A8A878;color:#A8A878}.type-tag.fighting{background:#C03028}.type-tag.fighting.disabled{border-color:#C03028;color:#C03028}.type-tag.flying{background:#A890F0}.type-tag.flying.disabled{border-color:#A890F0;color:#A890F0}.type-tag.poison{background:#A040A0}.type-tag.poison.disabled{border-color:#A040A0;color:#A040A0}.type-tag.ground{background:#E0C068}.type-tag.ground.disabled{border-color:#E0C068;color:#E0C068}.type-tag.rock{background:#B8A038}.type-tag.rock.disabled{border-color:#B8A038;color:#B8A038}.type-tag.bug{background:#A8B820}.type-tag.bug.disabled{border-color:#A8B820;color:#A8B820}.type-tag.ghost{background:#705898}.type-tag.ghost.disabled{border-color:#705898;color:#705898}.type-tag.steel{background:#B8B8D0}.type-tag.steel.disabled{border-color:#B8B8D0;color:#B8B8D0}.type-tag.fire{background:#F08030}.type-tag.fire.disabled{border-color:#F08030;color:#F08030}.type-tag.water{background:#6890F0}.type-tag.water.disabled{border-color:#6890F0;color:#6890F0}.type-tag.grass{background:#78C850}.type-tag.grass.disabled{border-color:#78C850;color:#78C850}.type-tag.electric{background:#F8D030}.type-tag.electric.disabled{border-color:#F8D030;color:#F8D030}.type-tag.psychic{background:#F85888}.type-tag.psychic.disabled{border-color:#F85888;color:#F85888}.type-tag.ice{background:#98D8D8}.type-tag.ice.disabled{border-color:#98D8D8;color:#98D8D8}.type-tag.dragon{background:#7038F8}.type-tag.dragon.disabled{border-color:#7038F8;color:#7038F8}.type-tag.dark{background:#705848}.type-tag.dark.disabled{border-color:#705848;color:#705848}.type-tag.fairy{background:#EE99AC}.type-tag.fairy.disabled{border-color:#EE99AC;color:#EE99AC}.type-tag.disabled{background:#FFF}.type-tag.disabled:hover{background:#EEE}

@-webkit-keyframes rotate{100%{transform:rotate(360deg)}}@keyframes rotate{100%{transform:rotate(360deg)}}@-webkit-keyframes resize{100%{transform:scaleX(0.2)}}@keyframes resize{100%{transform:scaleX(0.2)}}@-webkit-keyframes breathe{100%{opacity:.4}}@keyframes breathe{100%{opacity:.4}}.loader{position:absolute;top:calc(50% - 5vw);left:calc(50% - 5vw);width:10vw;height:10vw;border:1rem solid #39C;border-radius:50%;-webkit-animation:breathe 1s alternate infinite both, rotate 2s linear infinite;animation:breathe 1s alternate infinite both, rotate 2s linear infinite}.loader::before{content:'';position:absolute;top:-5vw;left:calc(50% - 5vw);width:10vw;height:10vw;background:radial-gradient(#E0E0E0 50%, rgba(224,224,224,0));transform-origin:top center;-webkit-animation:resize 1s linear alternate infinite both;animation:resize 1s linear alternate infinite both}

.filter-box{position:fixed;top:0;left:0;z-index:10;width:100%;min-height:18rem;background:#FFF;box-shadow:0 0 2rem rgba(0,0,0,0.2);font-size:1.2rem;transform:translateY(-100%);transition:transform .4s}.filter-box::after{content:'';position:absolute;bottom:0;left:0;z-index:20;width:100%;height:2rem;background:#FFF}.filter-box.open{transform:translateY(0)}.filter-box .toggle-btn{display:block;position:absolute;top:100%;right:0;width:20rem;height:5rem;background:#FFF;padding:0;border:none;border-bottom-left-radius:50rem;outline:none;box-shadow:0 0 2rem rgba(0,0,0,0.2);cursor:pointer;font-size:2rem;font-weight:700;line-height:5rem;text-align:center;text-transform:uppercase}.filter-box form{display:flex;justify-content:center;max-width:var(--max-width);padding:2rem;margin:auto;background:#FFF}.filter-box fieldset{flex:1 1 auto;border:0;width:-webkit-max-content;width:-moz-max-content;width:max-content}.filter-box legend{margin-bottom:1rem;font-size:1.6rem;text-transform:uppercase}.filter-box .input-name,.filter-box .select-gen{width:100%;height:4rem;padding:1rem;margin-bottom:1rem;border:none;border-radius:.8rem;box-shadow:0 0 0.8rem rgba(0,0,0,0.2);font-size:1.4rem}.filter-box .input-name:focus,.filter-box .select-gen:focus{outline:none;box-shadow:0 0 .8rem #0275FF88}.filter-box .input-name::-webkit-input-placeholder{color:#999}.filter-box .input-name:-ms-input-placeholder{color:#999}.filter-box .input-name::-ms-input-placeholder{color:#999}.filter-box .input-name::placeholder{color:#999}.filter-box .stat-input{display:flex;align-items:center;color:#666;font-size:1.4rem;text-transform:uppercase}.filter-box .types-container{margin:auto;min-width:18rem;max-width:36rem}.filter-box .type-tag{display:inline-block;cursor:pointer;margin:0 .2rem .2rem 0 !important}

.form-slider{position:relative;height:5px;margin-right:3rem}.form-slider::after{content:attr(value);display:block;width:2.4rem;height:5px;position:absolute;top:0;left:calc(100%);line-height:5px;text-align:right}

.header{height:19.4rem;padding:2rem;font-size:2rem;text-align:center}.header h1{margin-bottom:1rem}.header h2{margin:0;font-size:1.8rem;font-weight:400}

